/** @jsxImportSource @emotion/react */
import { css, keyframes } from "@emotion/react";

const opacity = keyframes`
        0%   {  opacity: 0; }
        100% {  opacity: 1;  }
`;

const background = css`
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;
  height: calc(var(--vh, 1vh) * 100);
  background-color: #1414148f;
  display: flex;
  align-items: center;
  justify-content: center;
  animation: ${opacity} 2s;
`;

const image = css`
  opacity: 0;
  animation: ${opacity} 2s 1s forwards;
  :hover {
    > img {
      opacity: 0.7;
    }
  }
  > img {
    width: 300px;
    aspect-ratio: 1/1;
    transition: opacity 0.3s;
  }
`;
type Props = { close: () => void };
const Loading = ({ close }: Props) => {
  return (
    <div css={background}>
      <button css={image} onClick={close}>
        <img src="/music-player/album-cover.jpg" alt="album" />
      </button>
    </div>
  );
};

export default Loading;
